import React, { Component } from 'react';
import indexCss from './index.module.scss';
import { baseURL } from '../../utils/request'
class Index extends Component {
    render() {
        const v = this.props.item;
        return (
            <div className={indexCss.house_item}>
                <div className={indexCss.house_img}>
                    <img src={baseURL + v.houseImg} alt="" />
                </div>
                <div className={indexCss.house_info}>
                    <div className={indexCss.house_title}>{v.title}</div>
                    <div className={indexCss.house_desc}>{v.desc}</div>
                    <div className={indexCss.house_tags}>
                        {v.tags.map((vv, ii) => <div className={indexCss.house_tags_item} key={ii}>{vv}</div>)}
                    </div>
                    <div className={indexCss.house_price}>
                        <span>{v.price}</span>
                        <span>元/月</span>
                    </div>
                </div>
            </div>
        );
    }
}

export default Index;