import React, { Component } from 'react';
import { NavBar, Icon } from 'antd-mobile';
import indexCss from './index.module.scss'
import { connect } from "react-redux";
import axios, { baseURL } from '../../utils/request';
import HouseItem from '../../component/HouseItem'
console.log('地图找房页面被加载')
const BMap = window.BMap;
let map = null;
class Index extends Component {
    state = {
        // 小区列表数据 
        houseList: [],
        // 小区列表是否展示
        showHouse: false
    }

    zoomFunc = (() => {
        let levelZoom = [
            {
                level: 0,
                zoom: 12,
                cls: 'circle'
            },
            {
                level: 1,
                zoom: 12,
                cls: 'circle'
            },
            {
                level: 2,
                zoom: 15,
                cls: 'circle'
            }
        ];
        let index = -1;
        return () => {
            index++;
            return levelZoom[index]
        }
    })()

    async componentDidMount() {
        const { cityName } = this.props;
        console.log(cityName)
        map = new BMap.Map("container");
        // 添加缩放控件
        map.addControl(new BMap.NavigationControl());
        // 添加比例尺
        map.addControl(new BMap.ScaleControl());
        map.addEventListener('dragstart',() => {this.setState({ showHouse: false });})
        // 获取城市id
        const id = (await axios.get(baseURL + '/area/info?name=' + cityName)).data.body.value;
        this.drawHouse(id, cityName)
    }

    // 描绘房源
    drawHouse = async (id, currentPosition) => {
        let zoomObj = this.zoomFunc()
        map.centerAndZoom(currentPosition, zoomObj.zoom);
        // 获取房源数据
        const houseList = (await axios.get(baseURL + '/area/map?id=' + id)).data.body;
        houseList.forEach(v => {
            var point = new BMap.Point(v.coord.longitude, v.coord.latitude);
            var opts = {
                position: point,    // 指定文本标注所在的地理位置
                //offset: new BMap.Size(30, -30)    //设置文本偏移量
            }
            var label = new BMap.Label(`<div class=${zoomObj.level < 2 ? indexCss.circle : indexCss.rect}><span>${v.label}</span><span>${v.count}套</span></div>`, opts);  // 创建文本标注对象
            label.setStyle({
                border: 'none',
                backgroundColor: 'transparent'
            });
            label.addEventListener('click', () => {
                if (zoomObj.level < 2) {
                    this.drawHouse(v.value, point);
                    setTimeout(() => {
                        map.clearOverlays();
                    }, 0);
                } else {
                    this.getHouse(v.value);
                    this.setState({ showHouse: true });
                }
            })
            map.addOverlay(label);
        });
    }

    // 获取小区的房源数据
    getHouse = async (id) => {
        const houseList = (await axios.get('/houses?cityId=' + id)).data.body.list;
        this.setState({ houseList });
    }

    render() {
        const { history } = this.props;
        const { houseList, showHouse } = this.state;
        return (
            <div className={indexCss.map_found}>
                {/* 头部导航栏 */}
                <div className={indexCss.map_found_nav}>
                    <NavBar
                        mode="light"
                        icon={<Icon type="left" />}
                        onLeftClick={() => history.go(-1)}
                    >城市选择</NavBar>
                </div>

                {/* 百度地图 */}
                <div className={indexCss.map}>
                    <div id="container" className={indexCss.map_container}></div>
                </div>

                {/* 房屋列表 */}
                {showHouse && <div className={indexCss.house_list}>
                    <div className={indexCss.house_list_title}>
                        <span>房屋列表</span>
                        <span>更多房源</span>
                    </div>
                    <div className={indexCss.house_list_content}>
                        {houseList.map((v,i) => <HouseItem houseList={houseList} item={v} key={i}/>)}
                    </div>
                </div>}
            </div>);
    }
}

const mapStateToProps = (state) => {
    return {
        cityName: state.mapReducer.city.name
    }
}

export default connect(mapStateToProps)(Index);

